import { LOGIN, LOGOUT } from "../actions/types";

const initialState = {
  name: null,
  isLoggedIn: false,
};

export function auth(state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case LOGIN:
      return { name: payload, isLoggedIn: true };

    case LOGOUT:
      return { name: null, loginState: false };

    default:
      return state;
  }
}
export default auth;
